import {ADD_ITEM, CHANGE_INPUT, DEL_ITEM, LOGIN, R_CHANGE_INPUT,TEXT_INPUT_CHANGE} from './actionType'

const defaultState = {
    list: [
        '8',
        '9',
        '5:30review'],
    data:{},
    inputValue :'',
    textValue:'',
};


export default (state = defaultState, action) => {
    if (action.type === CHANGE_INPUT) {
        let newState = JSON.parse(JSON.stringify(state));//state
        newState.inputValue = action.value;
        return newState
    }
    if (action.type === ADD_ITEM) {
        let newState = JSON.parse(JSON.stringify(state));
        newState.list.push(newState.inputValue);
        newState.inputValue = '';
        return newState;
    }
    if (action.type === DEL_ITEM) {
        let newState = JSON.parse(JSON.stringify(state));
        newState.list.splice(action.payload, 1);
        return newState;
    }
    //login
    if(action.type ===LOGIN) {
        let newState = JSON.parse(JSON.stringify(state));
        newState.data={
            ...action.payload
        };
        return newState;
    }
    //dispatch action
    if(action.type ===R_CHANGE_INPUT){
        let newState = JSON.parse(JSON.stringify(state));
        newState.inputValue=action.value;
        return newState;
    }
    if(action.type ===TEXT_INPUT_CHANGE){
        let newState = JSON.parse(JSON.stringify(state));
        newState.textValue = action.value;
        return newState;
    }
    //console.log("121231231231",state);
    return state;
}