import {applyMiddleware, compose, createStore} from 'redux' //  createStore
import reducer from './reducer'
import createSagaMiddleware from 'redux-saga' //redux-sage
import mySagas from './sagas'

const sagaMiddleware = createSagaMiddleware();//redux-sage

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ ?//devtools
    window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({}) : compose;
const enhancer = composeEnhancers(applyMiddleware(sagaMiddleware));
const store = createStore(reducer, enhancer);
sagaMiddleware.run(mySagas);


export default store